@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-bold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-regular.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  height: 100%;
}

html {
  scroll-behavior: smooth;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

[data-accordion=content].no-js {
  max-height: 100%;
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 15px 20px;
  border: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 3px;
  background: transparent;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

.accordion {
  display: flex;
  gap: 30px;
}
@media (max-width: 1023px) {
  .accordion {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .accordion {
    flex-direction: column;
    gap: 0;
    margin-bottom: 35px;
  }
}

.accordion__section {
  color: #ffffff;
}
@media (max-width: 1023px) {
  .accordion__section {
    width: 50%;
  }
}
@media (max-width: 767px) {
  .accordion__section {
    width: 100%;
    padding: 0 25px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
  .accordion__section:first-of-type {
    border-top: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.accordion__button {
  align-items: center;
  background-color: transparent;
  border: 0;
  color: inherit;
  display: flex;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  letter-spacing: 0.05em;
  line-height: 91.02%;
  margin: 0;
  margin-bottom: 20px;
  margin-top: 5px;
  min-height: 20px;
  padding: 0;
  pointer-events: none;
  text-align: left;
  text-transform: uppercase;
  width: 100%;
}
@media (max-width: 767px) {
  .accordion__button {
    align-items: center;
    cursor: pointer;
    margin: 0;
    margin-bottom: 20px;
    margin-top: 20px;
    min-height: 20px;
    pointer-events: all;
  }
  .accordion__button::after, .accordion__button::before {
    background-color: #ffffff;
    content: "";
    height: 1px;
    position: absolute;
    right: 26px;
    width: 15px;
  }
  .accordion__button::before {
    transform: rotate(90deg);
    transition: 0.3s transform;
  }
}
@media (hover: hover) {
  .accordion__button:hover:not(.focus-visible) {
    color: #00b2ff;
  }
  .accordion__button:hover:not(.focus-visible)::before, .accordion__button:hover:not(.focus-visible)::after {
    background-color: #00b2ff;
  }
}
.accordion__button.focus-visible:focus {
  color: #00b2ff;
}
.accordion__button.focus-visible:focus::before, .accordion__button.focus-visible:focus::after {
  background-color: #00b2ff;
}
.accordion__button.focus-visible:active {
  opacity: 0.5;
}

.accordion__section.is-active {
  padding-bottom: 20px;
}
.accordion__section.is-active .accordion__button::before {
  transform: rotate(0);
}

.header {
  position: sticky;
  top: 0;
  z-index: 10;
  width: 100%;
  min-height: 105px;
  color: #f2f5f7;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    position: relative;
    min-height: 88px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 60px;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-right: 5%;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 10px;
    padding-right: 50px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-right: 35px;
  }
}

.header__logo {
  display: block;
  width: 215px;
  height: 55px;
}
@media (max-width: 1023px) {
  .header__logo {
    padding-top: 10px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 55px;
    padding-right: 20px;
    padding-top: 15px;
  }
}

.header__contacts {
  display: grid;
  grid-template-columns: repeat(2, minmax(225px, 1fr));
  column-gap: 10%;
  width: 40%;
  padding: 15px 5px;
  margin-right: 0 auto;
  box-sizing: border-box;
}
@media (max-width: 1439px) {
  .header__contacts {
    width: 50%;
  }
}
@media (max-width: 1023px) {
  .header__contacts {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: wrap;
    max-width: none;
    width: auto;
    gap: 0;
    padding: 0;
  }
}

.header__contacts-link {
  display: inline-flex;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .header__contacts-link {
    padding: 20px 10px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header__contacts-link:last-child {
    margin-right: 0;
  }
}
@media (hover: hover) {
  .header__contacts-link:hover:not(.focus-visible) {
    color: #00b2ff;
    filter: drop-shadow(0 0 3px #00b2ff);
  }
}
.header__contacts-link.focus-visible:focus {
  color: #00b2ff;
  filter: drop-shadow(0 0 3px #00b2ff);
}
.header__contacts-link:active {
  opacity: 0.7;
}

.header__contacts-icon {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .header__contacts-icon {
    margin: 0;
  }
}

@media (max-width: 1023px) {
  .header__contacts-content {
    display: none;
  }
}

.header__button {
  color: #f2f5f7;
  border: 1px solid #f2f5f7;
  min-width: 160px;
  margin-top: 5px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}
@media (hover: hover) {
  .header__button:hover:not(.focus-visible) {
    color: #00b2ff;
    border-color: #00b2ff;
    filter: drop-shadow(0 0 3px #00b2ff);
  }
}
.header__button.focus-visible:focus {
  color: #00b2ff;
  border-color: #00b2ff;
  filter: drop-shadow(0 0 3px #00b2ff);
}
.header__button:active {
  opacity: 0.7;
}

@media (hover: hover) {
  .logo:hover:not(.focus-visible) {
    filter: drop-shadow(0 0 3px #00b2ff);
  }
}
.logo.focus-visible:focus {
  filter: drop-shadow(0 0 3px #00b2ff);
}
.logo:active {
  opacity: 0.7;
}

